var first = function() {
	console.log('works First');

	$('.slider-controls__button').on('click', function(e) {
	e.preventDefault();

	var $this = $(this),
		container = $this.closest('.slider'),
		list = container.find('.slider-list'),
		items = list.find('.slider-list__item'),
		activeItem = items.filter('.active-slide'),
		nextItem = activeItem.next(),
		prevItem = activeItem.prev();

		if($this.hasClass('right')) {
			if(nextItem.length) {
				moveSlide(container, nextItem.index());
			} else {
				moveSlide(container, 0);
			}
		} else {
			if(prevItem.length) {
				moveSlide(container, prevItem.index());
			} else {
				moveSlide(container, items.length - 1);
			}
		}
	});

	var moveSlide = function(container, slideNum) {
		var list = container.find('.slider-list'),
			items = list.find('.slider-list__item'),
			activeItem = items.filter('.active-slide'),
			reqItem = items.eq(slideNum),
			reqIndex = reqItem.index(),
			duration = 1000;

			if (reqItem.length) {
				list.animate({
					'left' : reqIndex * -100 + '%'
				}, duration, function () {
					activeItem.removeClass('active-slide');
					reqItem.addClass('active-slide');
				})
			}
	};
};

module.exports = {
	init: first
};