var second = function() {
  console.log('works Second');
};

ymaps.ready(init);
    var myMap,
    	myPlacemark;

    function init(){     
        myMap = new ymaps.Map("map", {
            center: [42.98739407162359,47.503982395888976],
            zoom: 13,
            controls: ["zoomControl"],
        });
    
		myPlacemark = new ymaps.Placemark([42.97768946886296,47.56904205775421], {}, {
	        iconLayout: 'default#image',
	        iconImageHref: '../shkola/img/decor/map-mark.png',
	        iconImageSize: [78, 118],
	        iconImageOffset: [-39, -130]
	    });

    	myMap.geoObjects.add(myPlacemark);
    	myMap.behaviors.disable('scrollZoom');
	};
module.exports = {
  init: second
};